import Config from '../config';
import { handleFetchs } from '../lib/url.js';
import store from '../store';

const massUpdate = payload => {
  const { type, value } = payload || {};
  const totems = store.getState().totems.data;

  let newTotems;
  if (type === "STATUS") {
    newTotems = totems.map(totem => {
      totem.enabled = value;
      return totem;
    });
  } else {
    newTotems = totems.map(totem => {
      totem.cash = value;
      return totem;
    });
  }

  return dispatch => {
    dispatch({ type: 'TOTEMS', totems: { ready: true, data: newTotems } });
  };
};

const updateTotem = payload => {
  const { id, type, value } = payload || {};
  const totems = store.getState().totems.data;

  let newTotems;
  if (type === "STATUS") {
    newTotems = totems.map(totem => {
      return totem.id === id ? { ...totem, enabled: value } : totem;
    });
  } else {
    newTotems = totems.map(totem => {
      return totem.id === id ? { ...totem, cash: value } : totem;
    });
  }

  return dispatch => {
    dispatch({ type: 'TOTEMS', totems: { ready: true, data: newTotems } });
  };
};

const loadTotems = () => {
  return dispatch => {
    dispatch({ type: 'TOTEMS', totems: { ready: false, data: [] } });

    return fetch(`${Config.URL_API_CLOUD}/configuration/station/667`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
    })
      .then(response => handleFetchs(response))
      .then(json => {
        const { totem_types, totems } = json.data;
        const parsedTotems = totems.map(totem => ({
          id: totem.id,
          name: totem.name,
          type: totem_types[totem.type - 1].name.split(' ').join('-').toLowerCase(),
          notification: true,
          enabled: true,
          cash: true,
        }));

        return dispatch({ type: 'TOTEMS', totems: { ready: true, data: parsedTotems } });
      })
      .catch(error => dispatch({ type: 'TOTEMS', totems: { ready: true, data: [] } }));
  };
};

export { massUpdate, updateTotem, loadTotems };
