import { createStore, applyMiddleware, compose } from 'redux';
import thunk from 'redux-thunk';

const initialState = {
  user: { logged: false, ready: false },
  stations: [
    { id: 1, name: 'Estación Mendoza', },
    { id: 2, name: 'Estación San Juan', },
    { id: 3, name: 'Estación San Luis', }
  ],
  totems: { ready: false, data: [] },
};

const reducer = (state = initialState, action) => {
  if (action.type === 'TOTEMS') {
    return {
      ...state,
      totems: action.totems,
    };
  }

  if (action.type === 'USER') {
    return {
      ...state,
      user: action.user,
    };
  }

  return state;
};

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;

export default createStore(reducer, window.REDUX_DATA || initialState, composeEnhancers(applyMiddleware(thunk)));
