import React from 'react';
import { Cell, Pie, PieChart } from 'recharts';

class PieChartWrapper extends React.Component {
  renderCustomizedLabel(data) {
    const { cx, cy, midAngle, innerRadius, outerRadius, percent } = data;
    const RADIAN = Math.PI / 180;

    const radius = innerRadius + (outerRadius - innerRadius) * 0.5;
    const x = cx + radius * Math.cos(-midAngle * RADIAN);
    const y = cy + radius * Math.sin(-midAngle * RADIAN);

    return (
      <text x={x} y={y} fill='#fff' textAnchor={x > cx ? 'start' : 'end'} dominantBaseline='central'>
        {`${(percent * 100).toFixed(0)}%`}
      </text>
    );
  }

  render() {
    const { data, dataKey, colors, height, width } = this.props;

    return (
      <PieChart height={height} width={width} >
        <Pie
          cx={height / 2}
          cy={height / 2}
          data={data}
          dataKey={dataKey}
          innerRadius={40}
          outerRadius={80}
          fill='#8884d8'
          label={this.renderCustomizedLabel}
          labelLine={false}
        >
          {data.map((_, index) =>
            <Cell key={`cell-${index}`} fill={colors[index % colors.length]} />
          )}
        </Pie>
      </PieChart>
    );
  }
}

export default PieChartWrapper;
