import React from 'react';

class BodyClassName extends React.Component {
  componentDidMount() {
    document.body.classList.add(this.getClassName());
    setTimeout(() => {
      window.dispatchEvent(new Event('resize'));
    }, 100);
  }

  componentWillUnmount() {
    document.body.classList.remove(this.getClassName());
  }

  getClassName() {
    let name = this.props.children.type.name;
    if (this.props.children.type.WrappedComponent) {
      name = this.props.children.type.WrappedComponent.name;
    }

    return `page-${name.toLowerCase()}`;
  }

  render() {
    return this.props.children;
  }
}

export default BodyClassName;
