import React from 'react';
import { CartesianGrid, Legend, Line, LineChart, Tooltip, XAxis, YAxis } from 'recharts';

class LineChartWrapper extends React.Component {
  render() {
    const { data, dataKey, height, lines, width } = this.props;

    return (
      <LineChart data={data} height={height} width={width}>
        <CartesianGrid strokeDasharray="3 3" />
        <XAxis dataKey={dataKey} padding={{ left: 30, right: 30 }} />
        <YAxis />
        <Tooltip />
        <Legend />
        {lines.map(line =>
          <Line key={line.key} type="monotone" dataKey={line.key} stroke={line.color} />
        )}
      </LineChart>
    );
  }
}

export default LineChartWrapper;
