import React from 'react';
import { Bar, BarChart, CartesianGrid, Legend, Tooltip, XAxis, YAxis, Cell, LabelList } from 'recharts';

class BarChartWrapper extends React.Component {
  getColor() {
    const { colors } = this.props;
    const index = Math.floor(Math.random() * colors.length);
    return colors[index];
  }

  render() {
    const { bars, data, dataKey, gradient, height, legend, tooltip, width } = this.props;
    return (
      <BarChart
        data={data}
        height={height}
        margin={{ top: 5, right: 30, left: 20, bottom: 5 }}
        width={width}
      >
        <CartesianGrid strokeDasharray="3 3" />
        <XAxis dataKey={dataKey} />
        <YAxis />
        {tooltip && <Tooltip />}
        {legend && <Legend />}
        {gradient &&
          <defs>
            <linearGradient id="splitColor" x1="0" y1="0" x2="0" y2="1">
              <stop offset="10%" stopColor={this.getColor()} stopOpacity={1} />
              <stop offset="90%" stopColor={this.getColor()} stopOpacity={1} />
            </linearGradient>
          </defs>
        }
        {bars.map(bar =>
          <Bar key={bar.key} dataKey={bar.key}>
            {data.map((_, index) => (
              <Cell
                key={`cell-${index}`}
                stroke='#8884d8'
                fill={gradient ? 'url(#splitColor)' : bar.color || this.getColor()}
              />
            ))}
            <LabelList dataKey={bar.key} position="top" />
          </Bar>
        )
        }
      </BarChart>
    );
  }
}

BarChartWrapper.defaultProps = {
  colors: ['#0088FE', '#00C49F', '#FFBB28', '#FF8042'],
}

export default BarChartWrapper;
