import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { Link } from 'react-router-dom';
import { logout } from '../actions/user';
import store from '../store';
import '../styles/sidebar.scss';

class Sidebar extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      items: [
        {
          collapsed: true,
          id: 'totems',
          label: 'Tótems',
        },
        {
          collapsed: true,
          id: 'reports',
          label: 'Reportes',
        },
      ]
    };

    this.handleCollapse = this.handleCollapse.bind(this);
  }

  handleCollapse(id) {
    const { items } = this.state;
    const newItems = items.map(item => ({
      ...item,
      collapsed: item.id === id ? !item.collapsed : item.collapsed,
    }));
    this.setState({ items: newItems });
  }

  logout() {
    store.dispatch(logout());
    this.props.history.push({ pathname: '/' });
  }

  renderChildren(pageLink) {
    const { handleSelectStation, location, stations, stationSelected } = this.props;
    const currentPage = location.pathname.includes('/reports') ? 'reports' : 'totems';

    return (
      <ul className="children">
        {stations.map(child => (
          <li
            key={child.id}
            className="item"
            onClick={() => handleSelectStation({ id: child.id, page: pageLink })}
          >
            <Link
              className={`${child.id === stationSelected && currentPage === pageLink ? 'child--active' : ''}`}
              to={`/${pageLink}`}>
              <span className="item__label">{child.name}</span>
            </Link>
          </li>
        ))}
      </ul>
    );
  }

  render() {
    const { location } = this.props;
    const { items } = this.state;
    const currentPage = location.pathname.includes('/reports') ? 'reports' : 'totems';

    return (
      <div className="sidebar">
        <div className="sidebar__logo" />
        <ul className="sidebar__nav">
          {items.map(item => (
            <li className="item" key={item.id}>
              <Link
                className={`${currentPage === item.id ? 'item--active' : ''}`}
                onClick={() => this.handleCollapse(item.id)}
                to={'#'}
              >
                <i className={`item__icon icon-${item.id === 'totems' ? 'home' : 'reports'}`} />
                <span className="item__label">{item.label}</span>
                <i className={`arrow item__icon icon-arrow-${item.collapsed ? 'down' : 'up'}`} />
              </Link>
              {!item.collapsed &&
                this.renderChildren(item.id)
              }
            </li>
          ))}
        </ul>
      </div>
    );
  }
}

function mapStateToProps(state) {
  return { stations: state.stations };
}

export default connect(mapStateToProps)(withRouter(Sidebar));
