import Config from '../config';
import cookie from 'react-cookies';
import { handleFetchs, fetchHeaders } from '../lib/url.js';

const login = (username, password) => {
  return dispatch => {
    dispatch({ type: 'USER', user: { logged: false, ready: false } });

    return fetch(`${Config.URL_API_FAKE}/login`, {
      method: 'POST',
      body: JSON.stringify({ username: username, password: password }),
      headers: {
        'Content-Type': 'application/json',
      },
    })
      .then(response => handleFetchs(response))
      .then(json => {
        cookie.save('token', json.token);
        dispatch({ type: 'USER', user: { ...{}, ...{ logged: true, ready: true }, ...json } });
      })
      .catch(error => dispatch({ type: 'USER', user: { logged: false, ready: true } }));
  };
};

const getUser = () => {
  return dispatch => {
    dispatch({ type: 'USER', user: { logged: false, ready: false } });

    return fetch(`${Config.URL_API_FAKE}/user`, {
      method: 'GET',
      headers: fetchHeaders(),
    })
      .then(response => handleFetchs(response))
      .then(json => dispatch({ type: 'USER', user: { ...{}, ...{ logged: true, ready: true }, ...json } }))
      .catch(error => dispatch({ type: 'USER', user: { logged: false, ready: true } }));
  };
};

const logout = () => {
  return dispatch => {
    cookie.remove('token');
    dispatch({ type: 'USER', user: { logged: false, ready: true } });
  };
};

export { login, logout, getUser };
