import React from 'react';
import { withRouter } from 'react-router-dom';
import '../styles/header.scss';
import UserBox from './userBox';
import { Link } from 'react-router-dom';

class Header extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      buttons: {
        reports: [
          { id: 'summary', label: 'resumen', },
          { id: 'sales', label: 'ventas y cajas', },
          { id: 'store', label: 'tienda', },
          { id: 'fuels', label: 'combustibles', },
          { id: 'employees', label: 'ventas por empleado', },
        ],
        totems: [
          { id: 'all', label: 'todos' },
          { id: 'self-service', label: 'self service' },
          { id: 'auto-full', label: 'auto full' },
          { id: 'peatonal', label: 'peatonal' },
        ],
      },
    }
  }

  renderOptions() {
    const { handleSelectOption, location, selectedOption } = this.props;
    const { buttons } = this.state;
    const currentPage = location.pathname.includes('/reports') ? 'reports' : 'totems';

    return (
      <div className="nav">
        {buttons && buttons[currentPage] &&
          buttons[currentPage].map(btn => (
            <Link
              key={btn.id}
              to={`/${currentPage}${btn.id !== 'all' && btn.id !== 'summary' ? `/${btn.id}` : ''}`}>
              <button
                className={`btn ${selectedOption === btn.id ? 'active' : ''}`}
                onClick={() => handleSelectOption(btn)}
              >
                {btn.label}
              </button>
            </Link>
          )
          )}
      </div>
    )
  }

  render() {
    return (
      <div className="header">
        {this.renderOptions()}
        <UserBox />
      </div>
    );
  }
}

export default withRouter(Header);
