import React, { Fragment } from 'react';
import { connect } from 'react-redux';
import Totem from '../components/totem';
import '../styles/totems.scss';
import CustomSwitch from '../components/switch';
import { massUpdate } from '../actions/totems';
import store from '../store';

class Totems extends React.Component {
  handleMassTotemStatus(data) {
    store.dispatch(massUpdate({ ...data, type: 'STATUS' }));
  }

  handleMassTotemCashPayment(data) {
    store.dispatch(massUpdate({ ...data, type: 'CASH' }));
  }

  renderHeadlines() {
    return (
      <div className="headlines">
        <div className="name"></div>
        <div className="switch">
          <span>Tótem habilitado</span>
        </div>
        <div className="switch">
          <span>Pago en efectivo</span>
        </div>
        <div className="switch">
          <span>Notificaciones</span>
        </div>
      </div>
    );
  }

  renderTotems() {
    const { match, totems: { data } } = this.props;
    let filteredTotems = [...data];

    if (match.params.category) {
      filteredTotems = filteredTotems.filter(t => t.type === match.params.category);
    }

    return (
      <Fragment>
        <h2>Tótems</h2>
        {this.renderHeadlines()}
        <div className="list">
          {filteredTotems && filteredTotems.map(totem =>
            <Totem key={totem.id} {...totem} />
          )}
        </div>
      </Fragment>
    );
  }

  render() {
    const { totems: { ready } } = this.props;

    return (
      ready && (
        <div className="totems">
          <h2>Acciones sobre todos los tótems</h2>
          <div className="totems__actions">
            <div className="action">
              <span className="action__label">Habilitar</span>
              <CustomSwitch checked={true} onChange={this.handleMassTotemStatus} />
            </div>
            <div className="action">
              <span className="action__label">Pago en efectivo</span>
              <CustomSwitch checked={true} onChange={this.handleMassTotemCashPayment} />
            </div>
          </div>

          {this.renderTotems()}
        </div>
      )
    );
  }
}

function mapStateToProps(state) {
  return { totems: state.totems };
}

export default connect(mapStateToProps)(Totems);
