import React from 'react';
import { connect } from 'react-redux';
import { Redirect, withRouter } from 'react-router-dom';
import { getToken } from '../lib/url';
import { getUser } from '../actions/user';
import store from '../store';

class CheckUser extends React.Component {
  componentDidMount() {
    let token = getToken();
    if (this.props.user.logged === false && token) {
      store.dispatch(getUser());
      return;
    }

    if (this.props.match.path === '/') {
      return;
    }
    if (this.props.user.logged === true) {
      return;
    }
    if (!token) {
      this.props.history.push({ pathname: '/' });
    }
  }

  render() {
    if (this.props.user.ready === true && this.props.user.logged === false) {
      return <Redirect to="/" />;
    }

    return null;
  }
}

function mapStateToProps(state) {
  return { user: state.user };
}

export default connect(mapStateToProps)(withRouter(CheckUser));
