import React, { Component } from 'react';
import { Provider as ReduxProvider } from 'react-redux';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';

import BodyClassName from './components/bodyClassName';
import CheckUser from './components/checkUser';
import Header from './components/header';
import Sidebar from './components/sidebar';
import { loadTotems } from './actions/totems';

import './styles/app.scss';
import routes from './routes';
import store from './store';

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      currentHeaderOption: 'all',
      stationSelected: 1,
    }

    this.handleSelectHeaderOption = this.handleSelectHeaderOption.bind(this);
    this.handleSelectStation = this.handleSelectStation.bind(this);
  }

  componentDidMount() {
    store.dispatch(loadTotems());
  }

  handleSelectHeaderOption(btn) {
    const { id } = btn;
    this.setState({ currentHeaderOption: id });
  }

  handleSelectStation(data) {
    const { id, page } = data;
    const currentHeaderOption = page === 'totems' ? 'all' : 'summary';
    this.setState({ stationSelected: id, currentHeaderOption });
  }

  render() {
    const { currentHeaderOption, stationSelected } = this.state;

    return (
      <ReduxProvider store={store}>
        <Router>
          <div className="container">
            <Sidebar
              handleSelectStation={this.handleSelectStation}
              stationSelected={stationSelected}
            />
            <Header
              handleSelectOption={this.handleSelectHeaderOption}
              selectedOption={currentHeaderOption}
            />
            <Switch>
              {routes.map((route, key) => (
                <Route
                  key={key}
                  path={route.path}
                  exact={route.exact}
                  render={props => {
                    const Component = route.component;
                    return (
                      <React.Fragment>
                        <CheckUser />
                        <BodyClassName {...props}>
                          <Component {...props} />
                        </BodyClassName>
                      </React.Fragment>
                    );
                  }}
                />
              ))}
            </Switch>
          </div>
        </Router>
      </ReduxProvider>
    );
  }
}

export default App;
