import React from 'react';
import CustomSwitch from './controlledSwitch';
import '../styles/totem.scss';
import { updateTotem } from '../actions/totems';
import store from '../store';

class Totem extends React.Component {
  handleTotemStatus(data) {
    store.dispatch(updateTotem({ ...data, type: 'STATUS' }));
  }

  handleTotemCashPayment(data) {
    store.dispatch(updateTotem({ ...data, type: 'CASH' }));
  }

  openNotification() {
    // TODO: Implement
  }

  render() {
    const { cash, enabled, id, name, notification } = this.props;

    return (
      <div className="totem">
        <div className="name">
          <span>{name}</span>
        </div>
        <div className="switch">
          <CustomSwitch
            checked={enabled}
            onChange={this.handleTotemStatus}
            id={id}
          />
        </div>
        <div className="switch">
          <CustomSwitch
            checked={cash}
            onChange={this.handleTotemCashPayment}
            id={id}
          />
        </div>
        <div className="switch">
          <div
            className={`notification${notification ? '' : ' notification--off'}`}
            onClick={this.openNotification()}
          >
            <span className={`badge${notification ? '' : ' badge--off'}`}>!</span>
          </div>
        </div>
      </div>
    );
  }
}

export default Totem;
