import Login from './pages/login';
import NotFound from './pages/notFound';
import Reports from './pages/reports';
import Totems from './pages/totems';

export default [
  {
    path: '/',
    exact: true,
    component: Login,
  },
  {
    path: '/totems/:category?',
    exact: true,
    component: Totems,
  },
  {
    path: '/reports/:section?',
    exact: true,
    component: Reports,
  },
  {
    component: NotFound,
  },
];
