import React from 'react';
import { connect } from 'react-redux';
import { login } from '../actions/user';
import { Redirect } from 'react-router-dom';
import store from '../store';
import '../styles/login.scss';
import { getTranslation } from '../lib/i18n';

class Login extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      username: '',
      password: '',
      isLoggingIn: false,
      errors: {},
    };
  }

  onChange(e) {
    const name = e.target.name;
    const value = e.target.value;
    this.setState({ isLoggingIn: false, [name]: value });
  }

  onSubmit(e) {
    e.preventDefault();
    if (this.validate() !== true) {
      return;
    }

    this.setState({ isLoggingIn: true });
    store.dispatch(login(this.state.username, this.state.password));
  }

  validate() {
    let result = true;
    let errors = {};

    this.setState({ errors: {} });

    if (!this.state.username) {
      errors.username = getTranslation('user_placeholder');
      result = false;
    }
    if (!this.state.password) {
      errors.password = getTranslation('password_placeholder');
      result = false;
    }

    if (!result) {
      this.setState({ errors: errors });
    }

    return result;
  }

  render() {
    if (this.props.user.logged === true) {
      return <Redirect to="/totems" />;
    }

    let error;
    if (this.state.isLoggingIn === true && this.props.user.ready === true && this.props.user.logged === false) {
      error = getTranslation('invalid_login');
    }

    return (
      <div className="login">
        <div className="logo"> </div>
        <h1>
          {getTranslation('welcome')}
          <span> {getTranslation('welcome_credentials')}</span>
        </h1>
        <form onSubmit={e => this.onSubmit(e)}>
          <fieldset className={this.state.errors.username ? 'hasError' : ''}>
            <label> {getTranslation('user')} </label>
            <input
              type="text"
              name="username"
              value={this.state.username}
              placeholder={getTranslation('user_placeholder')}
              onChange={e => this.onChange(e)}
            />
            <span className="error">{this.state.errors.username}</span>
          </fieldset>
          <fieldset className={this.state.errors.password ? 'hasError' : ''}>
            <label> {getTranslation('password')} </label>
            <input
              type="password"
              name="password"
              value={this.state.password}
              placeholder={getTranslation('password_placeholder')}
              onChange={e => this.onChange(e)}
            />
            <span className="error">{this.state.errors.password}</span>
            <a href="#!"> {getTranslation('forgot_password')} </a>
          </fieldset>
          <button type="submit"> {getTranslation('login')} </button>
        </form>
        <span className="error">{error}</span>
      </div>
    );
  }
}

function mapStateToProps(state) {
  return { user: state.user };
}

export default connect(mapStateToProps)(Login);
