import Config from '../config';

const translations = {
  es: {
    'forgot_password': '¿Olvidaste tu contraseña?',
    'invalid_login': 'Los datos ingresados son incorrectos',
    'login': 'Ingresar',
    'password': 'Contraseña',
    'password_placeholder': 'Ingresá tu contraseña',
    'user': 'Usuario',
    'user_placeholder': 'Ingresá tu usuario',
    'welcome': 'Bienvenido',
    'welcome_credentials': 'Ingresá tus credenciales para poder comenzar',
  },
  en: {
    'forgot_password': 'Forgot your password?',
    'invalid_login': 'User or password are invalid',
    'login': 'Login',
    'password': 'Password',
    'password_placeholder': 'Enter your password',
    'user': 'User',
    'user_placeholder': 'Enter your user',
    'welcome': 'Welcome',
    'welcome_credentials': 'Please enter your credentials to login',
  }
}

const getTranslation = (key, patternText) => {
  let text = translations[Config.LANG][key] || translations['es'][key];
  if (patternText) {
    text = text.replace('xxx', patternText);
  }
  return text;
};

export { getTranslation };
